import React from 'react';
import {inject, observer} from 'mobx-react';
import {Table, Input, Button, Form, Row, Col, Typography, message, Affix} from 'antd';
import * as mobx from 'mobx';

const {Title} = Typography;
const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

/*
* 
* */
class EditableCell extends React.Component {
	state = {
		editing: false,
	};

	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};

	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			this.toggleEdit();
			handleSave({...record, ...values});
		});
	};

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title} = this.props;
		const {editing} = this.state;
		return editing ? (
			<Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: true,
							message: `${title}`,
						},
					],
					initialValue: record[dataIndex],
				})(<Input ref={node => (this.input = node)} onPressEnter={this.save} onBlur={this.save}/>)}
			</Form.Item>
		) : (
			<div
				className="editable-cell-value-wrap"
				style={{paddingRight: 24}}
				onClick={this.toggleEdit}
			>
				{children}
			</div>
		);
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

@observer
class ManualModalTwo extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {};
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {

	}

	// table
	handleSave = row => {
		const newData = mobx.toJS(this.store.areaDataSource);
		const index = newData.findIndex(item => row.keyName === item.keyName);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row,
		});
		//console.log(row, newData, '')
		this.store.areaDataSource = Object.assign([], newData);
	};

	// 
	handleCancle = () => {
		// 
		const data = JSON.parse(JSON.stringify(this.store.defaultAreaData));
		this.store.areaDataSource = Object.assign([], data);
	}

	// 
	handleSubmit = () => {
		const params = mobx.toJS(this.store.areaDataSource);
		this.store.saveList({list: params}).then((res) => {
			if (res == true) {
				message.success("")
			} else {
				message.success("")
			}
		});
	}

	// 
	handleBack = () => {
		this.props.goBack();
	}

	render() {
		const {name} = this.props;
		const column = [     // 
			{
				title: "",
				dataIndex: 'region',
				align: 'center',
				className: 'vt w129',
				render: (text, row, index) => {
					if (index == 0) {
						return {
							children: <span></span>,
							props: {
								rowSpan: 5,
								className: 'tab_td_bg td_color vt w129'
							}
						};
					} else if (index == 5) {
						return {
							children: <span></span>,
							props: {
								rowSpan: 4,
								className: 'tab_td_bg td_color vt w129'
							}
						};
					} else if (index == 9) {
						return {
							children: <span></span>,
							props: {
								rowSpan: 10,
								className: 'tab_td_bg td_color vt w129'
							}
						};
					} else if (index == 19) {
						return {
							children: <span></span>,
							props: {
								rowSpan: 7,
								className: 'tab_td_bg td_color vt w129'
							}
						};
					} else if (index == 26) {
						return {
							children: <span></span>,
							props: {
								rowSpan: 5,
								className: 'tab_td_bg td_color vt w129'
							}
						};
					} else if (index == 31) {
						return {
							children: <span></span>,
							props: {
								rowSpan: 5,
								className: 'tab_td_bg td_color vt w129'
							}
						};
					} else {
						return {
							children: <span></span>,
							props: {
								rowSpan: 0,
								className: 'tab_td_bg td_color vt w129'
							}
						}
					}
				},
			},
			{
				title: "",
				dataIndex: 'keyName',
				align: 'center',
				className: 'vt w101',
				render: (text, row, index) => {
					const obj = {
						children: text,
						props: {
							className: 'td_bg1 w101'
						},
					};
					return obj;
				},
			},
			{
				title: "",
				dataIndex: 'keyValue',
				align: 'center',
				editable: this.props.title=="view"? false: true,
			}]
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const columns = column.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
				}),
			};
		});

		return (
			<div>
				<Title style={{
					fontSize: '15px',
					fontWeight: '600',
					margin: '20px 0'
				}} level={4}>{name}</Title>
				<Affix offsetTop={60}>
					<Table columns={columns}
					       dataSource={[]}
					       size={"small"}
					       pagination={false}
					       className="w400 ci_table2 ci_table3 ci_table6"
					       bordered/>
				</Affix>
				<Table
					components={components}
					className="w400 ci_table7"
					rowClassName={() => 'editable-row'}
					bordered
					pagination={false}
					dataSource={mobx.toJS(this.store.areaDataSource)}
					columns={columns}
					size={"small"}
					showHeader={false}
					rowKey={record => record.keyName}
				/>
				<Row className="tfc mt20">
					<Col>
						<Button onClick={this.handleBack} type="primary" className={this.props.title=="view"?"":"hidden"}></Button>
						<Button className={this.props.title=="view"?"hidden":"ml20"} type="default" onClick={this.handleCancle}></Button>
						<Button className={this.props.title=="view"?"hidden":"ml20"} type="primary" onClick={this.handleSubmit}></Button>
					</Col>
				</Row>
			</div>
		);
	}
}


export default ManualModalTwo;




