import React from 'react';
import {inject, observer} from 'mobx-react';
import {Table, Input, Button, Form, Row, Col, Typography, message} from 'antd';
import * as mobx from 'mobx';

const { Title } = Typography;
const EditableContext = React.createContext();
const EditableRow = ({ form, index, ...props }) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

/*
* 
* */
class EditableCell extends React.Component {
	state = {
		editing: false,
	};

	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({ editing }, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};

	save = e => {
		const { record, handleSave } = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			this.toggleEdit();
			handleSave({ ...record, ...values });
		});
	};

	renderCell = form => {
		this.form = form;
		const { children, dataIndex, record, title } = this.props;
		const { editing } = this.state;
		return editing ? (
			<Form.Item style={{ margin: 0 }}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: true,
							message: `${title}`,
						},
					],
					initialValue: record[dataIndex],
				})(<Input ref={node => (this.input = node)} onPressEnter={this.save} onBlur={this.save} />)}
			</Form.Item>
		) : (
			<div
				className="editable-cell-value-wrap"
				style={{ paddingRight: 24 }}
				onClick={this.toggleEdit}
			>
				{children}
			</div>
		);
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

const transObj = {
	"": "",
	"": "",
	"": "",
}

@observer
class ManualModalOne extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.columns = [     // 
			{
				title: transObj[this.props.name],
				dataIndex: 'keyValue',
				align: 'center',
				editable: true,
			}
		];
		this.state = {

		};
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {

	}

	// table
	handleSave = row => {
		const newData = [...this.store.dataSource];
		const index = newData.findIndex(item => row.key === item.key);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row,
		});
		//console.log(row, newData, '')
		this.store.dataSource = Object.assign([], newData);
	};

	// 
	handleCancle = () => {
		// 
		const data  = JSON.parse(JSON.stringify(this.store.defaultData));
		this.store.dataSource = Object.assign([], data);
	}

	// 
	handleSubmit = () => {
		const params = mobx.toJS(this.store.dataSource)[0];
		this.store.save({yearTarget:params}).then((res)=>{
			if (res==true) {
				message.success("")
			} else {
				message.success("")
			}
		});
	}

	// 
	handleBack = () => {
		this.props.goBack();
	}

	render() {
		const {name} = this.props;
		const column = [     // 
			{
				title: transObj[name],
				dataIndex: 'keyValue',
				align: 'center',
				editable: this.props.title=="view"? false: true
			}]
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const columns = column.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
				}),
			};
		});
		const obj = {
			"": "",
			"": "",
			"": "",
		}
		return (
			<div>
				<Title style={{
					fontSize:'15px',
					fontWeight: '600',
					margin: '20px 0'
				}} level={4}>{obj[name]}</Title>
				<Table
					components={components}
					className="w300"
					rowClassName={() => 'editable-row'}
					bordered
					pagination={false}
					dataSource={mobx.toJS(this.store.dataSource)}
					columns={columns}
				/>
				<Row className="tfc mt20">
					<Col>
						<Button onClick={this.handleBack} type="primary" className={this.props.title=="view"?"":"hidden"}></Button>
						<Button className={this.props.title=="view"?"hidden":"ml20"} type="default" onClick={this.handleCancle}></Button>
						<Button className={this.props.title=="view"?"hidden":"ml20"} type="primary" onClick={this.handleSubmit}></Button>
					</Col>
				</Row>
			</div>
		);
	}
}


export default ManualModalOne;




