import React from 'react';
import {inject, observer} from 'mobx-react';
import {Breadcrumb, Row, Col, Form, Select, message} from 'antd';
import ManualStore from '@/store/report/ManualStore';
import moment from 'moment';
import url from 'url';
import ManualModalOne from '@/components/report/ManualModalOne';
import ManualModalTwo from '@/components/report/ManualModalTwo';

import DatePicker from 'bee-datepicker';
import 'bee-datepicker/build/DatePicker.css';
import zhCN from "bee-datepicker/build/locale/zh_CN";
const { YearPicker } = DatePicker;


//const {MonthPicker} = DatePicker;
const {Option} = Select;
const obj = {
	add: '',
	edit: '',
	view: ''
}

@Form.create()
@observer
class ManualReporting extends React.Component {
	constructor(props) {
		super(props);
		this.store = new ManualStore();
		this.state = {
			reportTypeList: [],
			reportNameList: [],
			value: moment().format("YYYYMM"),
			name: '', // 
			title: '', // 
		}
	}

	componentDidMount() {
		const {form} = this.props;
		let search = this.props.location.search;
		const {title, classifyName, theme, month} = url.parse(search, true).query;
		if (title=="add") { // 
			this.getReportType();
			// 
			this.setState({
				title: title
			})
		}
		if (title=="edit" || title=="view") {  // 
			// 
			//console.log(url.parse(search, true).query)
			const params = {
				"level": 1,
				"parentId": ""
			}
			this.store.getReportClassifys(params).then((res) => {
				if (res) {
					this.setState({
						reportTypeList: Object.assign([], res.filter((res) => {   // 
							return res.name == "" || res.name == "";
						}))
					}, ()=>{
						// 
						const id = this.state.reportTypeList.filter((item)=>{return item.name===theme})&&this.state.reportTypeList.filter((item)=>{return item.name===theme})[0]['id'];
						//this.getReportName(id);

						const {form} = this.props;
						const params = {
							"level": 2,
							"parentId": id
						}
						this.store.getReportClassifys(params).then((res) => {
							if (res) {
								let tempData = res;
								if (id == "1") { //   6
									this.setState({
										reportNameList: Object.assign([], tempData.filter((item) => {
											return item.name == "" || item.name == "" || item.name == "" || item.name == "" || item.name == "";
										}))
									}, () => {
										form.setFieldsValue({["reportType"]: theme});
										form.setFieldsValue({["reportName"]: classifyName});

										this.setState({
											name: classifyName
										});

										// 
										this.getYearTarget(classifyName);
									});
								} else if (id == "2") {
									this.setState({
										reportNameList: Object.assign([], tempData.filter((item) => {
											return item.name == "";
										}))
									}, () => {

										form.setFieldsValue({["reportType"]: theme});
										form.setFieldsValue({["reportName"]: classifyName});

										this.setState({
											name: classifyName
										});
										// 
										this.getYearTarget(classifyName);
									});
								}
							}
						});

					});
				}
			});

			// 
			this.setState({
				value: month,
				title: title
			})
		}
	}

	// 
	getReportType() {
		const params = {
			"level": 1,
			"parentId": ""
		}
		this.store.getReportClassifys(params).then((res) => {
			if (res) {
				this.setState({
					reportTypeList: Object.assign([], res.filter((res) => {   // 
						return res.name == "" || res.name == "";
					}))
				})
			}
		});
	}

	// 
	getReportName(parentId) {
		const {form} = this.props;
		const params = {
			"level": 2,
			"parentId": parentId
		}
		this.store.getReportClassifys(params).then((res) => {
			if (res) {
				let tempData = res;
				if (parentId == "1") { //   6
					this.setState({
						reportNameList: Object.assign([], tempData.filter((item) => {
							return item.name == "" || item.name == "" || item.name == "" || item.name == "" || item.name == "";
						}))
					}, () => {
						form.setFieldsValue({["reportName"]: this.state.reportNameList[0]['name']});
						this.setState({
							name: this.state.reportNameList[0]['name']
						});

						// 
						this.getYearTarget(this.state.reportNameList[0]['name']);
					});
				} else if (parentId == "2") {
					this.setState({
						reportNameList: Object.assign([], tempData.filter((item) => {
							return item.name == "";
						}))
					}, () => {
						form.setFieldsValue({["reportName"]: this.state.reportNameList[0]['name']});
						this.setState({
							name: this.state.reportNameList[0]['name']
						});
						// 
						this.getYearTarget(this.state.reportNameList[0]['name']);
					});
				}
			}
		});
	}

	handleChange = (value, e) => {
        const id = e.props.val.id;
		this.getReportName(id);
	}

	disabledDate = (current) => {
		return current && current > moment().endOf('date');
	}

	/*handleDownload = (value, dateString) => {
		this.setState({
			value: dateString
		});
	}*/

	onYearChange = (d, dataString) => {
		// 
		const year = moment().year();
		if (dataString > year) {  //
			message.warn("")
            return false;
		} else {
			// 
			this.setState({
				value: dataString
			});


		}
	};

	// 
	getYearTarget = (param) => {
		switch (param) {
			case '':
				const params = {
					yearName: moment(this.state.value+"11").format("YYYY"),
					tableName:'rlcbztqk',
					schemaName:'yearCost',
					keyName:'hr',
				}
				this.store.getYearTarget(1, {yearTarget:params});
				break;
			case '':
				const param2 = {
					yearName: moment(this.state.value+"11").format("YYYY"),
					tableName:'jcfyztqk',
					schemaName:'yearCost',
					keyName:'',
				}
				this.store.getYearTarget(1, {yearTarget:param2});
				break;
			case '':
				const param3 = {
					yearName: moment(this.state.value+"11").format("YYYY"),
					tableName:'zxfyztqk',
					schemaName:'yearCost',
					keyName:'',
				}
				this.store.getYearTarget(1, {yearTarget:param3});
				break;
			case '':
				const param4 = {
					yearName: moment(this.state.value+"11").format("YYYY"),
					tableName:'yycb',
					schemaName:'area',
					keyName:'',
					keyValue:'',
				}
				this.store.getYearTarget(2, {yearTarget:param4});
				break;
			case '':
				const param5 = {
					yearName: moment(this.state.value+"11").format("YYYY"),
					tableName:'yysr',
					schemaName:'area',
					keyName:'',
					keyValue:'',
				}
				this.store.getYearTarget(2, {yearTarget:param5});
				break;
			case '':
				const param6 = {
					yearName: moment(this.state.value+"11").format("YYYY"),
					tableName:'htqk',
					schemaName:'area',
					keyName:'',
					keyValue:'',
				}
				this.store.getYearTarget(2, {yearTarget:param6});
				break;
			default:
					break;
		}
	}

	// 
	handleReportName = (value, e) => {
		const param = e.props.val.name;
		this.setState({
			name: param
		});
		this.getYearTarget(param);
	}

	goBack = () => {
		this.props.history.goBack();
	}

	render() {
		const {reportTypeList, reportNameList, value, name, title} = this.state;
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 6},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
		};

		return (
			<div>
				<div className="con_bottom">
					<Breadcrumb separator=">" style={{margin: '0'}}>
						<Breadcrumb.Item></Breadcrumb.Item>
						<Breadcrumb.Item href="#/report"></Breadcrumb.Item>
						<Breadcrumb.Item>{obj[title]}</Breadcrumb.Item>
					</Breadcrumb>
				</div>
				<Form layout="inline" {...formItemLayout} onSubmit={this.handleSubmit}>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{"marginTop": "15px"}}>
						<Col className="gutter-row" xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('reportType', {
									rules: [{required: false, message: ''}],
								})(
									<Select
										placeholder=""
										className="w"
										style={{width: '250px'}}
										onChange={this.handleChange}>
										{
											reportTypeList.map((item, index) => {
												return (<Option key={index} value={item.name} val={item}>{item.name}</Option>)
											})
										}
									</Select>
								)}
							</Form.Item>
						</Col>
						<Col className="gutter-row" xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label=""  className="w">
								{getFieldDecorator('reportName', {
									rules: [{required: false, message: ''}],
								})(
									<Select
										placeholder=""
										className="w"
										style={{width: '250px'}}
										onChange={this.handleReportName}>
										{
											reportNameList.map((item, index) => {
												return (<Option key={index} value={item.name} val={item}>{item.name}</Option>)
											})
										}
									</Select>
								)}
							</Form.Item>
						</Col>
						<Col className="gutter-row" xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="" className="w">
								{getFieldDecorator('reportMonth', {
									initialValue: moment(value, 'YYYYMM'),
									rules: [{required: false, message: ''}],
								})(
									<YearPicker
										className="w manna_year"
										style={{width: '250px'}}
										format="YYYY"
										onChange={this.onYearChange}
										locale={zhCN}
										placeholder=""
										defaultValue={moment()}
										showClose={false}
									/>
								)}
							</Form.Item>
							{/*<MonthPicker
										className="w"
										style={{width: '250px'}}
										disabledDate={this.disabledDate}
										onChange={this.handleDownload}
										placeholder=""/>*/}
						</Col>
					</Row>
				</Form>
				{name==""||name==""||name==""?<ManualModalOne
					name={name}
				    store={this.store}
					title={title}
					goBack={this.goBack}
				/>:null}
				{name==""||name==""||name==""?<ManualModalTwo
					name={name}
					store={this.store}
					title={title}
					goBack={this.goBack}
				/>:null}
			</div>
		)
	}
}


export default ManualReporting;




